.form {
  position: relative;
  display: block;

  @media screen and (min-width: $tablet) {
    display: flex;
    justify-content: center;
  }

  &__icon {
    position: absolute;
    top: 0;
    right: 0;

    @media screen and (min-width: $tablet) {
      right: 155px;
    }

    @media screen and (min-width: $desktop) {
      right: 410px;
    }
  }
}

.form__input {
  display: block;
  width: 100%;
  height: 20px;

  @include fonts(14px, 16px, 400);
  color: var(--light-font-cl);

  background-color: transparent;
  outline: none;
  border: none;
  border-bottom: 0.5px solid var(--light-font-cl);

  &::placeholder {
    @include fonts(14px, 16px, 400);
    color: var(--light-font-cl);
  }

  @media screen and (min-width: $tablet) {
    width: 394px;
  }

  transition: border-color var(--animation-slow),
    box-shadow var(--animation-slow), transform var(--animation-slow);

  &:hover,
  &:focus {
    border-color: var(--accent-cl);
    box-shadow: inset 3px -11px 25px -13px #a7151a;
    transform: scale(1.1);
  }
}
